<template>
  <div>
    <div class="question-detail">
      <div class="question-detail-contents">
        <div class="question-detail-texts">
          <div class="question-detail-category">
            <div>{{ question.mainCategory }} > {{ question.subCategory }}</div>
            <div v-if="checkNickname()" class="question-buttons">
              <div class="question-modify-btn" @click="modifyQuestion()">
                <img src="@/assets/edit-button.png" width="20px" />
              </div>
              <div class="question-delete-btn" @click="deleteQuestionCheck()">
                <img
                  style="width: 12px;"
                  src="data:image/svg+xml;base64,"
                />
              </div>
            </div>
          </div>
          <div class="question-detail-title">
            {{ question.title }}
          </div>
          <div class="question-detail-user font-break">
            <img
              @click="goUserProfile(question.nickname)"
              class="question-detail-profileImg"
              :src="question.profileUrl"
              alt="profile-image"
            />
            <span @click="goUserProfile(question.nickname)" class="question-detail-nickname">{{
              question.nickname
            }}</span>
          </div>
          <div class="question-detail-content" v-html="modifyUrl(question.content)">
            <!-- {{question.content}} -->
          </div>
          <div class="question-detail-info font-break">
            {{ humanize(now, question.regDt) + "\u00A0\u00A0\u00A0" }}조회수{{
              "\u00A0" + question.readCount
            }}
          </div>
        </div>
        <div class="question-detail-counts font-break">
          <div class="question-detail-count">
            <img
              style="width:20px; margin: 13px 0 3px 0;"
              src="data:image/svg+xml;base64,"
            />
            <div>{{ answerCount }}</div>
          </div>
          <div class="question-detail-count question-count-button" @click="changeLike()">
            <img
              v-if="isLiked"
              style="width:20px; margin: 13px 0 3px 0;"
              src="data:image/svg+xml;base64,"
            />
            <img
              v-else
              style="width:20px; margin: 13px 0 3px 0;"
              src="data:image/svg+xml;base64,"
            />
            <div>{{ likeCount }}</div>
          </div>
          <div class="question-detail-count question-count-button" @click="changeBookmark()">
            <img
              v-if="isBookmarked"
              style="width:20px; margin: 13px 0 3px 0;"
              src="data:image/svg+xml;base64,"
            />
            <img
              v-else
              style="width:20px; margin: 13px 0 3px 0;"
              src="data:image/svg+xml;base64,"
            />
            <div>{{ bookmarkCount }}</div>
          </div>
        </div>
      </div>
    </div>
    <hr style="color: #e0e0e0; opacity: 0.8;" />
    <Answer :questionNickname="question.nickname" :questionState="question.type" />
    <AnswerCreate v-if="isLoggedIn" :questionId="$route.params.questionId" />
    <div v-else class="nonlogin-answer" @click="$router.push({ name: 'Login' })">
      <div class="nonlogin-answer-content">
        <span>로그인하고 여러분의 지식을 공유해보세요!</span>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import API from "@/API.js";
import Answer from "@/components/questions/Answer";
import AnswerCreate from "@/components/questions/AnswerCreate";
import LoginModal from "@/components/main/LoginModal";
import router from "@/router";
import { mapState, mapGetters, mapActions } from "vuex";
import QuestionDelete from "./QuestionDeleteModal.vue";

export default {
  name: "QuestionsDetail",
  components: {
    Answer,
    AnswerCreate,
  },
  data() {
    return {
      question: {},
      answerCount: 0,
      likeCount: 0,
      bookmarkCount: 0,
      isLiked: false,
      isBookmarked: false,
      now: new Date(),
    };
  },
  methods: {
    ...mapActions(["setStateQuestionId", "setStateQuestion", "setStateContent"]),
    getQuestionDetail: function() {
      axios({
        url: API.URL + `questions/${this.$route.params.questionId}/info`,
        method: "get",
        headers: { Authorization: "Bearer " + this.accessToken },
      })
        .then((res) => {
          this.question = res.data;
          this.answerCount = res.data.answerCount;
          this.likeCount = res.data.likeCount;
          this.bookmarkCount = res.data.bookmarkCount;
          this.isLiked = res.data.liked;
          this.isBookmarked = res.data.bookmarked;
        })
        .catch(() => {
          this.$router.push({name: 'ErrorPage'})
        });
    },
    changeLike: function() {
      if (this.isLoggedIn) {
        if (this.isLiked) {
          axios({
            url: API.URL + `questions/${this.$route.params.questionId}/recommend`,
            method: "delete",
            headers: { Authorization: "Bearer " + this.accessToken },
          })
            .then(() => {
              this.isLiked = !this.isLiked;
              this.likeCount -= 1;
            })
            .catch((err) => {
              console.log(err);
            });
        } else {
          axios({
            url: API.URL + `questions/${this.$route.params.questionId}/recommend`,
            method: "post",
            headers: { Authorization: "Bearer " + this.accessToken },
          })
            .then(() => {
              this.isLiked = !this.isLiked;
              this.likeCount += 1;
            })
            .catch((err) => {
              console.log(err);
            });
        }
      } else {
        this.$modal.show(
          LoginModal,
          {
            modal: this.$modal,
          },
          {
            name: "dynamic-modal",
            width: "600px",
            height: "250px",
            draggable: false,
          }
        );
      }
    },
    changeBookmark: function() {
      if (this.isLoggedIn) {
        if (this.isBookmarked) {
          axios({
            url: API.URL + `questions/${this.$route.params.questionId}/bookmark`,
            method: "delete",
            headers: { Authorization: "Bearer " + this.accessToken },
          })
            .then(() => {
              this.isBookmarked = !this.isBookmarked;
              this.bookmarkCount -= 1;
            })
            .catch((err) => {
              console.log(err);
            });
        } else {
          axios({
            url: API.URL + `questions/${this.$route.params.questionId}/bookmark`,
            method: "post",
            headers: { Authorization: "Bearer " + this.accessToken },
          })
            .then(() => {
              this.isBookmarked = !this.isBookmarked;
              this.bookmarkCount += 1;
            })
            .catch((err) => {
              console.log(err);
            });
        }
      } else {
        this.$modal.show(
          LoginModal,
          {
            modal: this.$modal,
          },
          {
            name: "dynamic-modal",
            width: "600px",
            height: "250px",
            draggable: false,
          }
        );
      }
    },
    humanize: function(now, date) {
      const moment = require("moment");
      const dateData = new Date(date);
      let r = now - dateData;
      if (parseInt(r) > 43200000) {
        r = moment(dateData).format("YYYY.MM.DD");
      } else if (parseInt(r) >= 3600000) {
        r = parseInt(parseInt(r) / 3600000).toString() + "시간 전";
      } else if (parseInt(r) >= 60000) {
        r = parseInt(parseInt(r) / 60000).toString() + "분 전";
      } else {
        r = "방금 전";
      }
      return r;
    },
    modifyUrl(url) {
      if (url == null) {
        return "";
      }

      let endpoint = url;
      endpoint = endpoint.replaceAll("<oembed", "<iframe allowfullscreen");
      endpoint = endpoint.replaceAll("url", "src");
      endpoint = endpoint.replaceAll("watch?v=", "embed/");
      endpoint = endpoint.replaceAll("</oembed", "</iframe");
      endpoint = endpoint.replaceAll(`youtu.be/`, `www.youtube.com/embed/`);
      endpoint = endpoint.replaceAll(`<img src=`, `<img style="max-width:870px;" src=`);

      return endpoint;
    },
    deleteQuestion() {
      axios({
        url: API.URL + `questions/${this.$route.params.questionId}`,
        method: "delete",
        headers: { Authorization: "Bearer " + this.accessToken },
      })
        .then(() => {
          router.push({ path: "/questions" });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    deleteQuestionCheck() {
      this.$modal.show(
        QuestionDelete,
        {
          question: this.$route.params.questionId,
          modal: this.$modal,
        },
        {
          name: "dynamic-modal",
          width: "600px",
          height: "250px",
          draggable: false,
        }
      );
    },
    modifyQuestion() {
      this.setStateQuestion(this.question);
      this.setStateQuestionId(this.$route.params.questionId);
      this.$router.push({
        name: "QuestionsModify",
      });
    },
    checkNickname() {
      if (this.question.nickname == this.userNickname) return true;

      return false;
    },
    goUserProfile(nickname) {
      if (nickname !== this.userNickname) {
        this.$router.push({
          name: "Profile",
          params: {
            nickname: nickname,
          },
        });
      } else {
        this.$router.push({
          path: `/my-profile/${this.userNickname}`,
        });
      }
    },
  },
  created() {
    this.getQuestionDetail();
  },
  computed: {
    ...mapState({
      questionChangeTrigger: (state) => state.questions.questionChangeTrigger,
      accessToken: (state) => state.auth.accessToken,
      userNickname: (state) => state.auth.userNickname,
    }),
    ...mapGetters(["isLoggedIn"]),
  },
  watch: {
    questionChangeTrigger: function() {
      this.getQuestionDetail();
    },
  },
};
</script>

<style>
iframe {
  width: 800px;
  height: 450px;
}

.nonlogin-answer {
  display: flex;
  justify-content: center;
  margin: 50px 0 100px 0;
}

.nonlogin-answer-content {
  align-items: center;
  border: 1px solid #658dc6;
  border-radius: 20px;
  color: #658dc6;
  cursor: pointer;
  display: flex;
  font-size: 17px;
  font-weight: 700;
  height: 200px;
  justify-content: center;
  transition: 0.2s;
  width: 900px;
}

.nonlogin-answer-content:hover {
  /* background-color: #0f4c81; */
  background-color: #658dc6;
  color: white;
  transform: scale(1.02);
}

/* .nonlogin-answer-content span {
  color: #658dc6
} */

.question-count-button {
  cursor: pointer;
}

.question-count-button:hover {
  filter: brightness(107%);
}

.question-detail {
  display: flex;
  min-height: 300px;
  margin-top: 60px;
  justify-content: center;
}

.question-detail-category {
  display: flex;
  color: #84898c;
  font-size: 17px;
  margin-bottom: 10px;
  text-align: left;
  justify-content: space-between;
}

.question-detail-content {
  font-size: 17px;
  padding: 30px 0 0 20px;
  text-align: left;
  width: 850px;
}

.question-detail-contents {
  max-width: 900px;
  min-width: 900px;
}

.question-detail-count {
  border: 1px solid #e0e0e0;
  border-radius: 70%;
  height: 70px;
  margin-bottom: 20px;
  width: 70px;
}

.question-detail-counts {
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  justify-content: flex-end;
}

.question-detail-info {
  color: #84898c;
  font-size: 14px;
  margin-top: 100px;
  padding-left: 20px;
  text-align: left;
}

.question-detail-nickname {
  cursor: pointer;
  color: #84898c;
  margin-left: 10px;
  font-size: 16px;
}

.question-detail-profileImg {
  cursor: pointer;
  border-radius: 70%;
  height: 30px;
  object-fit: cover;
  width: 30px;
}

.question-detail-texts {
  float: left;
  min-height: 300px;
  width: 1000px;
}

.question-detail-user {
  margin: 15px 0 0 20px;
  text-align: left;
}

.question-detail-title {
  border-left: 3px solid #0f4c81;
  font-size: 30px;
  padding-left: 15px;
  text-align: left;
  width: 850px;
}

.question-buttons {
  display: flex;
  align-items: center;
  margin-right: 90px;
}

.question-buttons div {
  display: flex;
  width: 20px;
}

.question-modify-btn {
  /* background-color: #658dc6; */
  border-radius: 6px;
  color: white;
  cursor: pointer;
  float: left;
  height: 30px;
  font-size: 15px;
  width: 20px;
  margin-right: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.question-delete-btn {
  /* background-color: #b5c7d3; */
  border-radius: 6px;
  color: white;
  cursor: pointer;
  float: left;
  top: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.question-modify-btn:hover {
  filter: brightness(40%);
}
</style>
